/* General Styles */
.contact-page {
  font-family: 'Arial', sans-serif;
  margin: 0 auto;
  max-width: 1200px;
  padding: 40px;
  background-color: #f5f5f5;
  color: #333;
}

.contact-header {
  text-align: center;
  margin-bottom: 40px;
}

.contact-header h2 {
  font-size: 25px;
  color: #ff6600;
}

.contact-header p {
  font-size: 18px;
  color: #666;
}

.marquee-section {
  background-color: #f0f8ff;  /* Light background color */
  padding: 15px;  /* Padding to give space around text */
  border-radius: 8px;  /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);  /* Light shadow for depth */
  margin: 20px 0;  /* Top and bottom margin for spacing */
  font-family: 'Roboto', sans-serif;  /* Clean and modern font */
  font-size: 18px;  /* Size of the text */
  color: #333;  /* Dark text color for contrast */
  text-align: center;  /* Center the marquee text */
}

marquee {
  font-weight: bold;  /* Make the text bold */
  color: yellowgreen;  /* Dark green color for the text */
  text-transform: uppercase;  /* Make text uppercase */
}

/* Team Section */
.team-section {
  text-align: center;
  margin-top: 60px;
}

.team-section h3 {
  font-size: 28px;
  color: #ff6600;
  margin-bottom: 40px;
}

.team-members {
  display: flex;
  justify-content: space-around;
  gap: 40px;
  flex-wrap: wrap;
}

.team-member {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  width: 250px;
  text-align: center;
  transition: transform 0.3s ease;
}

.team-member:hover {
  transform: translateY(-10px);
}

.team-member img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 15px;
  object-fit: cover;
}

.team-member h4 {
  font-size: 22px;
  color: #333;
}

.team-member p {
  font-size: 16px;
  color: #666;
}

/* Contact Details */
.contact-details {
  text-align: center;
  margin-top: 60px;
}

.contact-details h3 {
  font-size: 28px;
  color: #ff6600;
  margin-bottom: 20px;
}

.contact-details p {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

/* Links Section */
.links-section {
  text-align: center;
  margin-top: 40px;
}

.links-section h3 {
  font-size: 28px;
  color: #ff6600;
  margin-bottom: 20px;
}

.links-section p {
  font-size: 18px;
  color: #333;
}

.links-section a {
  color: #ff6600;
  text-decoration: none;
  font-weight: bold;
}

.links-section a:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .team-members {
    flex-direction: column;
    align-items: center;
  }

  .team-member {
    width: 80%;
    margin-bottom: 30px;
  }

  .contact-form {
    width: 90%;
  }

  .contact-details p {
    font-size: 16px;
  }
}
