.footer {
  background-color: #1d1d1d; /* Dark background */
  color: #f0f0f0; /* Light text */
  padding: 40px 20px;
  font-family: 'Roboto', sans-serif;
  text-align: left;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  gap: 40px;
  flex-wrap: wrap;
}

.footer-left,
.footer-links,
.footer-government-links {
  flex: 1 1 30%;
}

.footer-logo {
  font-size: 26px;
  font-weight: bold;
  color: #ff6600; /* Bright orange for the logo */
  margin-bottom: 10px;
}

.footer-left p {
  margin: 8px 0;
  font-size: 16px;
}

.footer-links h4,
.footer-government-links h4 {
  font-size: 20px;
  color: #ff6600; /* Bright orange heading */
  margin-bottom: 15px;
}

.footer-links ul,
.footer-government-links ul {
  list-style-type: none;
  padding: 0;
}

.footer-links a,
.footer-government-links a {
  text-decoration: none;
  color: #f0f0f0; /* Light text color */
  font-size: 16px;
  display: block;
  margin: 8px 0;
}

.footer-links a:hover,
.footer-government-links a:hover {
  color: #ff6600; /* Change color to orange on hover */
}

.footer-bottom {
  text-align: center;
  margin-top: 30px;
  font-size: 14px;
  color: #bbb; /* Light grey for footer text */
}

.footer-bottom p {
  margin: 0;
}

/* Responsive design for mobile */
@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
  }

  .footer-left,
  .footer-links,
  .footer-government-links {
    flex: 1 1 100%;
    text-align: center;
  }

  .footer-links ul,
  .footer-government-links ul {
    padding-left: 0;
  }
}

@media (max-width: 480px) {
  .footer-logo {
    font-size: 22px;
  }

  .footer-links a,
  .footer-government-links a {
    font-size: 14px;
  }
}
